<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">Rem</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">
                    <p>rem 是相对于 html 元素的 font-size 的一个单位。</p>
                    <p>如果 html 上定义了 font-size: 50px; 则无论在任何地方都是 1rem = 50px 这个大小不会受到父元素的影响。</p>
                    <p>我们统一使用rem对页面进行整体缩放。所以强烈建议大家对需要适应页面大小的任何元素都使用rem为单位来定义。</p>
                    <p>我们在 iphone6 上使用 1rem = 50px 来换算。大于 320px 小于 414px 宽度的设备进行等比缩放</p>
                </div>
            </li>
            <li>
                <h3>Demo：</h3>
                <div class="text">
                    <a href="https://xingchou.github.io/rem/rem.js">https://xingchou.github.io/rem/rem.js</a>
                </div>
            </li>
            <li>
                <h3>Usage：</h3>
                <div class="text">
                    在head标签加载其它css和js之前引入
                </div>
            </li>
        </ul>
    </div>
</template>

